import React, { Component, Suspense } from 'react';
import { componentWillAppendToBody } from "react-append-to-body";
import { v4 as uuidv4 } from 'uuid';
import { connect } from 'react-redux';
import { CloudUrl, SocialCloudUrl } from './constants';
import { brandingUrl, detectMobile, innerWallStudio } from './utils';
import { isTagEmbed, isTagShop, isTagShopEditor, isSocialWall, isSocialEmbed, isEmbed } from './actions/api'
import { onUpdateEditor } from './actions/themeActions'
import AOS from "aos";
const App = React.lazy(() => import('./app'));
const ErrorPage = React.lazy(() => import('./app/Components/ErrorPages'));
const AppHead = React.lazy(() => import('./app/Components/Head/Head'));
// const SocialFonts = React.lazy(() => import('./app/Components/Head/SocialFonts'));
const UserComponents = React.lazy(() => import('./app/Components/UserComponents'));
const PhotoBooth = React.lazy(() => import('./app/Components/PhotoBooth'));
const SharePost = React.lazy(() => import('./app/Components/UserComponents/SharePost'));
const PostPopUp = React.lazy(() => import('./app/Components/PostPopUp'));
const ReelPopUp = React.lazy(() => import('./app/Components/ReelPopUp'));
const ReportMedia = React.lazy(() => import('./app/Components/UserComponents/ReportMedia'));
const CustomBranding = React.lazy(() => import('./app/Components/CustomBranding'));
const SocialWallBranding = React.lazy(() => import('./app/Components/CustomBranding/SocialWall'));
const UpgradePopUp = React.lazy(() => import('./app/Components/CustomBranding/UpgradePopUp'));
const Studio = React.lazy(() => import('./app/Components/Studio'));
const Fullscreen = React.lazy(() => import('./app/Components/Fullscreen'));
const Announcements = React.lazy(() => import('./app/Components/Announcements'));
const Spotlight = React.lazy(() => import('./app/Components/Spotlight'));
const ReviewHub = React.lazy(() => import('./app/Components/UserComponents/ReviewHub'));


export const addScript = url => {
  const script = document.createElement("script");
  script.src = url;
  script.defer = true;
  document.head.appendChild(script);
}

export const isEditor = window.isEditor ? true : false

export const onLoad = () => {
  if (!window.jQuery) addScript(`${SocialCloudUrl}/js/jquery.min.js?ver=1`);
  addScript(`${SocialCloudUrl}/js/slackdown.js?ver=1`);
}

const MyComponent = ({ children }) => {
  return children;
}
const TaggboxThemes = (props) => {
  if (props.loader && props.loader.loader != null && props.error.errorWithMessage == null && (!props.error.planLimit || isTagShop) && (props.error.themeError == null || (Object.keys(props.error.themeError).length > 0 && props.error.themeError.error_code == 0) || props.wall?.Personalization.demoWall === 1))
    return <Suspense fallback={<></>}><App {...props} /></Suspense>
  else if (!isTagShop && (props.error.errorWithMessage || props.error.planLimit || props.error.themeError))
    return <Suspense fallback={<></>}><ErrorPage {...props} /></Suspense>
  else
    return null
}
const AppendedView = componentWillAppendToBody(MyComponent);

const BackLinks = ({ appData }) => {
  const isBranding = appData.wall.UserDetail && Object.keys(appData.wall.UserDetail).length > 0 ? true : false
  let brandNet = appData.webFilters != '' && appData.webFilters ? appData.webFilters.map(({ networkId }) => networkId) : null;
  const isStatus = isBranding && !appData.wall.UserDetail.planId.includes('53') ? true : isTagEmbed && appData.wall.refere && Object.keys(appData.wall.refere).length > 0 ? true : false
  const hrefLink = isTagShop && appData.wall.User.referePageLink ? appData.wall.User.referePageLink : isTagEmbed ? appData.wall.refere.referePageLink : brandingUrl(brandNet, appData.wallID)[0];
  const labelText = isTagShop && appData.wall.User.referePage ? appData.wall.User.referePage : isTagEmbed ? appData.wall.refere.referePage : brandingUrl(brandNet, appData.wallID)[1]
  return isStatus ? <a href={hrefLink} target="_blank" style={{ display: 'none' }}
    aria-label={labelText}>{labelText}</a> : null
}
class WidgetApp extends Component {
  state = {
    errorData: null,
    render: ""
  }
  componentWillMount() {
    if (isSocialWall) {
      AOS.init();
      import("aos/dist/aos.css");
    }
  }
  componentDidMount() {
    setTimeout(() => onLoad(), 200)
    this.onLoadEditor()

  }

  componentDidUpdate(prevProps, prevState) {
    let demoWall = prevProps.appData.wall?.Personalization?.demoWall
    if (prevProps.appData.error?.themeError !== prevState.errorData) {
      this.setState({ errorData: prevProps.appData.error?.themeError })
      if (typeof window.onSetErrorCode === 'function' && this.state.errorData !== null) {
        window.onSetErrorCode(this.state.errorData, demoWall)
      }
    }
  }
  ///for tagshop editor preview
  onLoadEditor = () => {
    if (isTagShopEditor) {
      //const channel = new BroadcastChannel('embedLivePreview')
      try {
        window.addEventListener('message', this.handleMessage);
        // channel.addEventListener("message", (event) => {
        //   const payload = JSON.parse(event?.data);
        //   console.warn("payload 1", payload)
        //   this.setState({ render: uuidv4() })
        //   const { onUpdateEditor } = this.props
        //   onUpdateEditor(payload)
        // });
      } catch (error) {
        console.log("error", error)
      }
    }
    else if (window.location.href.includes("addfeedload=load")) {
      window.addEventListener('message', this.addFeedReloadTheme);
    }
  }
  componentWillUnmount() {
    if (isTagShopEditor) window.removeEventListener('message', this.handleMessage);
    else if (window.location.href.includes("addfeedload=load")) window.removeEventListener('message', this.addFeedReloadTheme);
  }
  handleMessage = (event) => {
    const { type, data } = event.data
    if (type == "reloadWebsite") {
      const payload = JSON.parse(data);
      console.warn("payload 1", payload)
      this.setState({ render: uuidv4() })
      const { onUpdateEditor } = this.props
      onUpdateEditor(payload)
    }
  }


  addFeedReloadTheme = (e) => {
    const { type, data } = e.data
    if (type == "reloadTheme") {
      const { onUpdateEditor } = this.props
      onUpdateEditor(data)
    }
  }

  render() {

    const { appData, modalPop, spotlight } = this.props;
    const { render } = this.state
    const isBranding = appData && appData.wall && Object.keys(appData.wall).length > 0 && appData.wall.UserDetail && Object.keys(appData.wall.UserDetail).length > 0 ? true : false
    const spotlightStatus = isSocialWall && spotlight.status ? true : false
    const snapType = (appData.wall?.UgcSettings?.onsite_status && appData.wall?.PhotoBoothSettings?.photo_booth_status) ? 1 : appData.wall?.PhotoBoothSettings?.photo_booth_status ? 3 : 2;

    return <>

      {isSocialWall && !window.location.href.includes("onsite-upload") && appData.isStudio == 1 && !innerWallStudio && !isEditor && !window.location.href.includes("editor=1") ? <><Studio wallID={appData.wallID} /></> : <>
        {!isSocialEmbed ? <Suspense fallback={<></>}><Fullscreen /></Suspense> : null}
        {/* <Suspense fallback={<></>}><SocialFonts /></Suspense> */}
        {/* {modalPop.onSiteIsShowPopUp && appData?.wall ? <AppendedView>  <Suspense fallback={<></>}> { isSocialWall ? <ReviewHub wall={appData?.wall} wallId={appData?.wall?.Wall?.id} feeds={appData?.feeds} isFreeAdsStatus={appData.postData.isFreeAdsStatus} /> : <UserComponents /> } </Suspense> </AppendedView> : null} */}
        {appData && appData.wall && Object.keys(appData.wall).length > 0 && !window.location.href.includes("onsite-upload") ? <Suspense fallback={<></>}><AppHead wall={appData.wall} /></Suspense> : null}
        {modalPop.onSiteIsShowPopUp && appData?.wall ? <AppendedView>  <Suspense fallback={<></>}> { (isSocialWall && snapType != 2) ? <PhotoBooth wall={appData?.wall} wallId={appData?.wall?.Wall?.id} feeds={appData?.feeds} isFreeAdsStatus={appData.postData.isFreeAdsStatus} snapType={snapType} /> : <ReviewHub wall={appData?.wall} wallId={appData?.wall?.Wall?.id} feeds={appData?.feeds} isFreeAdsStatus={appData.postData.isFreeAdsStatus} /> } </Suspense> </AppendedView> : null}

        <TaggboxThemes {...appData} key={render} keyRender={render} spotlight={spotlight} />
        {isBranding && appData.wall.UserDetail.planId.includes('53') && !isSocialWall ? <Suspense fallback={<></>}><CustomBranding appData={appData} /> </Suspense> : null}
        {appData?.postData?.isFreeAdsStatus ? <AppendedView>
          <Suspense fallback={<></>}><SocialWallBranding appData={appData} /> </Suspense>
          {!window.location.href.includes("editor=1") ? <Suspense fallback={<></>}><UpgradePopUp appData={appData} /> </Suspense> : ''}
        </AppendedView> : null}

        {!spotlightStatus && modalPop.isShowPopUp ? isTagShop && detectMobile() ? <AppendedView> <Suspense fallback={<></>}><ReelPopUp wall={appData.wall} data={modalPop.data} languageSetting={appData.languageSetting} networkData={appData.networkData} postData={appData.postData} brandingNetworks={appData.webFilters != '' && appData.webFilters ? appData.webFilters.map(({ networkId }) => networkId) : null} /></Suspense> </AppendedView> : <AppendedView> <Suspense fallback={<></>}><PostPopUp wall={appData.wall} isShowPopUp={modalPop.isShowPopUp} data={modalPop.data} languageSetting={appData.languageSetting} networkData={appData.networkData} postData={appData.postData} brandingNetworks={appData.webFilters != '' && appData.webFilters ? appData.webFilters.map(({ networkId }) => networkId) : null} key={modalPop.isShowPopUp} /></Suspense> </AppendedView> : null}

        {isSocialWall && !spotlightStatus && appData.announcements ? <Suspense fallback={<></>}><Announcements announcements={appData.announcements} /></Suspense> : null}


        {modalPop.reportStatus && isEmbed ? <AppendedView> <Suspense fallback={<></>}><ReportMedia item={modalPop.reportData} wall={appData.wall} /></Suspense> </AppendedView> : null}

        {modalPop.shareStatus && isEmbed ? <AppendedView> <Suspense fallback={<></>}><SharePost postLink={modalPop.shareData.link} userName={modalPop.shareData.author.name} /></Suspense> </AppendedView> : null}
        {appData && appData.wall && Object.keys(appData.wall).length > 0 && isEmbed ? <AppendedView><BackLinks appData={appData} /></AppendedView> : null}
        {spotlightStatus ? <Suspense fallback={<></>}><Spotlight itemData={spotlight.data} wall={appData.wall} /></Suspense> : null}

      </>}
    </>
  }
}
const mapStateToProps = state => {
  return {
    appData: state.appData,
    modalPop: state.modalPop,
    spotlight: state.spotlight
  }
}
const mapDispatchToProps = dispatch => {
  return {
    onUpdateEditor: (updateData) => dispatch(onUpdateEditor(updateData))
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(WidgetApp);

